import { createStore } from 'vuex';
import axios from 'axios';


export const store = createStore ({
    state: {
        products: [],
        cart: JSON.parse(localStorage.getItem('userCart')) || [],
        sliderItems: [],
        user: JSON.parse(localStorage.getItem('userData')) || null
    },
    mutations: {
        SET_PRODUCTS_TO_STATE: (state, products) => {
            state.products = products;
        }, 
        SET_SLIDER_ITEMS_TO_STATE: (state, sliderItems) => {
            state.sliderItems = sliderItems;
        },

        SET_CART: (state, product) => {
            if(state.cart.length) {
                let isProductExist = false;

                state.cart.map( function(item) {
                    if (item.article === product.article) { //1 m q-0
                        if (item.sizes === product.sizes) {
                            isProductExist = true;
                            item.quantity += 1;
                        } else if (!item.quantity){
                            isProductExist = true;
                            product.quantity = 1;
                            state.cart.push(product);
                        }
                    }
                })
                if (!isProductExist) {
                    product.quantity = 1;
                    state.cart.push(product);
                }
            } else {
                product.quantity = 1;
                state.cart.push(product);
            }
            localStorage.setItem('userCart', JSON.stringify(state.cart));
        },

        REMOVE_ITEM: (state, index) => {
            if (state.cart[index].quantity === 1) {
                state.cart.splice(index, 1);
            } else {
                state.cart[index].quantity--;
            }
            localStorage.setItem('userCart', JSON.stringify(state.cart))
        },
        INCREMENT: (state, index) => {
            state.cart[index].quantity += 1;
            localStorage.setItem('userCart', JSON.stringify(state.cart))
        },
        DECREMENT: (state, index) => {
            if(state.cart[index].quantity > 1) {
                state.cart[index].quantity -= 1;
            } else {
                confirm('Delete item?') ? state.cart.splice(index, 1) : state.cart[index].quantity;
            }
            localStorage.setItem('userCart', JSON.stringify(state.cart))
        },
        CLEAR_CART(state) {
            state.cart = [];
            localStorage.removeItem('userCart')
        },
        SET_USER(state, userData) {
            localStorage.setItem('userData', JSON.stringify(userData))
            state.user = {
                ...userData
            }
        },
        DELETE_USER(state) {
            localStorage.removeItem('userData');
            state.user = null
        }

    },
    actions: {
        GET_PRODUCTS_FROM_API({commit}) {
            return axios('http://localhost:3000/products', {
                method: "GET"
            })
            .then((products) => {
                commit('SET_PRODUCTS_TO_STATE', products.data);
                return products;
            })
            .catch((error) => {
                console.log(error);
                return error;
            })
        },
        GET_SLIDER_ITEMS_FROM_API({commit}) {
            return axios(`http://localhost:3000/slider-items`, {
                method: "GET"
            })
            .then((sliderItems) => {
                commit('SET_SLIDER_ITEMS_TO_STATE', sliderItems.data);
                return sliderItems;
            })
            .catch((error) => {
                console.log(error);
                return error;
            })
        },

        ADD_TO_CART({commit}, product) {
            commit('SET_CART', product)
        },

        DELETE_FROM_CART({commit}, index) {
            commit('REMOVE_ITEM', index)
        },
        INCREMENT_CART_ITEM({commit}, index) {
            commit('INCREMENT', index)
        },
        DECREMENT_CART_ITEM({commit}, index) {
            commit('DECREMENT', index)
        },
        SET_USER_TO_STATE({commit}, userData) {
            commit('SET_USER', userData)
        },
        LOGOUT_USER({commit}) {
            commit('DELETE_USER')
        }
    },
    getters: { //    
        GET_PRODUCTS(state) {
            return state.products;
        },
        GET_CART(state) {
            return state.cart || localStorage.getItem('userCart');
        },
        GET_SLIDER_ITEMS(state) {
            return state.sliderItems;
        },
        GET_USER(state) {
            if (state.user) {
                return state.user
            }
            const userData = JSON.parse(localStorage.getItem('userData'))
            if(userData) {
                state.user = {
                    ...userData
                }
                return userData
            }
            return null
        },
    }
})

export default store;