<!-- to make it independend and multi-use need to change getter GET_SLIDER_ITEMS
to prop and call this method in parrent component. in this case its not neccesary
because slider used only on main page -->
<template>
    <div class="slider-wrapper">
        <div class="slider-items-wrapper"
        >
            <vSliderItems
            v-for="sliderItem in this.sliderItemsData"
            :key="sliderItem.id"
            :sliderItem_data="sliderItem"
            :style="('transform: translateX('+ translate +'%);')"
            />
        </div>  
        <div class="slider-buttons-wrapper">
            <button class="slider-button"
            @click="prevSlide"
            > 
                <img src="../assets/images/slider-images/slider-navigation-arrow.svg" alt=""> 
            </button>
            <button class="slider-button"
            @click="nextSlide"
            > 
                <img src="../assets/images/slider-images/slider-navigation-arrow.svg" alt=""> 
            </button>
        </div>
    </div>
</template>

<script>
import vSliderItems from '../components/v-slider-item.vue'
import { mapActions } from 'vuex';

export default {
    name: "v-slider",
    components: {
        vSliderItems
    },
    props: {
        sliderItemsData: {
            type: Array,
            default: () => [],
        },
        interval: {
            type: Number,
            default: () => 0,
        },
        directionOfSlider: {
            type: String,
            default: () => '-',
        }
    },
    data () {
        return {
            currentSliderIndex: 0,
            direction: this.directionOfSlider,
            translate: 0
        }
    },
    methods: {
        ...mapActions ([
            'GET_SLIDER_ITEMS_FROM_API'
        ]),
        slidePosition() {
            if (this.directionOfSlider === 'left') {
                this.translate = 0;
                this.currentSliderIndex = 0;
            } else if (this.directionOfSlider === 'right') {
                this.translate = (this.sliderItemsData.length-1)  * -100;
                this.currentSliderIndex = this.sliderItemsData.length-1;
            }
            return this.translate
        },
        prevSlide() {
            if (this.currentSliderIndex > 0) {
                this.currentSliderIndex -=1;
                this.translate = this.currentSliderIndex * -100;
            } else { 
                if (this.directionOfSlider === 'right') {
                    this.currentSliderIndex = this.sliderItemsData.length-1
                    this.translate = this.currentSliderIndex * -100;
                }
            }
        },
        nextSlide() {
            if (this.currentSliderIndex < this.sliderItemsData.length-1) {
                this.currentSliderIndex += 1;
            } else {
                if (this.directionOfSlider === 'right') {
                    this.currentSliderIndex = this.sliderItemsData.length-1;
                } else {
                    this.currentSliderIndex = 0;
                }
            }
            this.translate = this.currentSliderIndex * -100;
        },
        autoSlide() {
            if (this.directionOfSlider === 'left') {
                if (this.currentSliderIndex >= this.sliderItemsData.length-1) {
                    this.currentSliderIndex = 0;
                } else {
                    this.currentSliderIndex += 1;
                }
                this.translate = this.currentSliderIndex * -100;
            } else if (this.directionOfSlider === 'right') {
                if (this.currentSliderIndex === 0) {
                    this.currentSliderIndex = this.sliderItemsData.length-1
                } else {
                    this.currentSliderIndex -= 1;
                }
                this.translate = this.currentSliderIndex * -100;
            }
        },
    },
    created() {
        this.GET_SLIDER_ITEMS_FROM_API()
        .then(() => {
            this.slidePosition()
        })
        .catch(err => 
        console.log(err));

        if (this.interval > 0) {
            let vm = this;

            setInterval(() => {
                vm.autoSlide();                
            }, vm.interval);
        }
    }
}
</script>

