<template>
    <div class="v-autorization">
        <form v-if="!isLogin && !isSubmited"
        @submit.prevent="submitAutorization" 
        class="autorization-form"
        >
            <p class="autorization-form-title">
                Lets create your profile! <br>
                Fill fields in form
            </p>

            <div class="autorization-input-wrapper">
                <label for="name">Name: </label> 
                <input type="text"
                v-model="newUserData.name"
                id="name" 
                name="name"
                placeholder="Name" 
                class="autorization-confirmation-input" 
                required
                >
            </div>    
            <div class="autorization-input-wrapper">
                <label for="lastname">Lastname: </label> 
                <input type="lastname"
                v-model="newUserData.lastname"
                id="lastname" 
                name="lastname"
                placeholder="Lastname" 
                class="autorization-confirmation-input" 
                required
                >
            </div>
            <div class="autorization-input-wrapper">
                <label for="phone">Phone number: </label> 
                <input type="tel" 
                v-model="newUserData.phone"
                id="phone" 
                name="phone"
                placeholder="Phone number" 
                class="autorization-confirmation-input" 
                required
                >
            </div>    
            <div class="autorization-input-wrapper">
                <label for="email">Email: </label> 
                <input type="email" 
                v-model="newUserData.email"
                id="email" 
                name="email"
                placeholder="Email@.com" 
                class="autorization-confirmation-input" 
                required
                >
            </div>
            <div class="autorization-input-wrapper">
                <label for="password">Password: </label> 
                <input type="password" 
                v-model="newUserData.password"
                id="password"
                name="password"
                placeholder="Password" 
                class="autorization-confirmation-input"
                required
                >
            </div>
            <div class="autorization-input-wrapper">
                <label for="country">Country: </label> 
                <select
                v-model="newUserData.country"
                id="country"
                name="country"
                class="autorization-confirmation-input select" 
                required
                >
                    <option value="" class="default-option" disabled selected>Not chosen</option>
                    <option value="Ukraine">Ukraine</option>
                    <option value="Great Britain">Great Britain</option>
                    <option value="Poland">Poland</option>
                    <option value="Germany">Germany</option>
                    <option value="France">France</option>
                    <option value="Italy">Italy</option>
                    <option value="USA">USA</option>
                </select>
            </div> 
            <div class="autorization-input-wrapper">
                <label for="address">Adress of shipping: </label> 
                <input type="text" 
                v-model="newUserData.address"
                id="address"
                name="address"
                placeholder="Address" 
                class="autorization-confirmation-input"
                required
                >
            </div>

            <p 
            v-if="!isLogin"
            class="autorization error-hadler"
            >
                {{ formError }}
            </p>

            <div class="autorization submit-buttons">

                <button 
                v-if="formIsValid"
                class="autorization btn confirm"
                type="submit"
                >SUBMIT
                </button>

                <button 
                v-else
                disabled
                class="autorization btn confirm disabled"
                >SUBMIT
                </button>

                <button 
                class="autorization btn cancel"
                @click="cancelRegistration"
                >
                    CANCEL
                </button>
            </div>

            <div class="login-wrapper">
                <p>
                    Or you can login
                </p>
    
                <button
                class="login btn"
                type="submit"
                @click="isLogin = !isLogin"
                >
                LOGIN
                </button>
            </div>
        </form>

        <p 
        v-else-if="!isLogin"
        class="login error-hadler">
            {{ formError }}
        </p>

        <form
        v-if="isLogin && !isSubmited"
        @submit.prevent="submitLogin" 
        class="autorization-form"
        >
            <p class="autorization-form-title">
                Welcome back!
            </p>

            <div class="autorization-input-wrapper">
                <label for="email">Email: </label> 
                <input type="text"
                v-model="userData.email"
                id="email" 
                name="email"
                placeholder="Email" 
                class="autorization-confirmation-input" 
                required
                >
            </div>    
            <div class="autorization-input-wrapper">
                <label for="password">Password: </label> 
                <input type="password"
                v-model="userData.password"
                id="password" 
                name="password"
                placeholder="password" 
                class="autorization-confirmation-input" 
                required
                >
            </div> 

            <p class="error-hadler">
                {{ loginFormError }}
            </p>
            <div class="autorization submit-buttons login">

                <button 
                v-if="loginFormIsValid"
                class="autorization btn confirm"
                type="submit"
                >SUBMIT
                </button>

                <button 
                v-else
                disabled
                class="autorization btn confirm disabled"
                >SUBMIT
                </button>

                <button 
                class="autorization btn cancel"
                @click="isLogin = !isLogin"
                >
                    BACK
                </button>
            </div>
        </form>


        <p 
        v-else
        class="error-hadler">
            {{ loginFormError }}
        </p>

    </div>
</template>

<script>
import axios from 'axios'
import { mapActions, mapGetters } from 'vuex'

export default {
    name: 'vAutorization',
    data () {
        return {
            newUserData: {
                name: '',
                lastname: '',
                phone: null,
                email: '',
                country: '',
                address: '',
                password: ''
            },
            userData: {
                email: '',
                password: ''
            },
            formError: '',
            formIsValid: false,
            loginFormIsValid: false,
            loginFormError: '',
            isSubmited: false,
            isLogin: false
        }
    },
    computed: {
        ...mapGetters([
            'GET_USER'
        ])
    },
    methods: {
        ...mapActions([
            'SET_USER_TO_STATE',
            'LOGOUT_USER'
        ]),
        formValidation() {
            //check is fields is filled 
            if (this.isSubmited) {
                return
            }
            if (
                !this.newUserData.name || 
                !this.newUserData.lastname || 
                !this.newUserData.phone || 
                !this.newUserData.email || 
                !this.newUserData.country ||
                !this.newUserData.address
            ) {
                this.formIsValid = false
                this.formError = 'Please, fill all fields'
                return
            }
             //check type of number
            let phone = parseInt(this.newUserData.phone)
            if (isNaN(phone)) {
                this.formIsValid = false
                this.formError = 'Please enter valid phone number'
                return
            }
            this.formError = ''
            //check email validity
            const emailPattern = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
            if (!emailPattern.test(this.newUserData.email)) {
                this.formIsValid = false
                this.formError = 'Please enter valid email'
                return
            }
            this.formError = ''
            this.formIsValid = true
        },
        loginValidation() {
            if (this.isSubmited) {
                return
            }
            if (
                !this.userData.email || 
                !this.userData.password
            ) {
                this.loginFormIsValid = false
                this.loginFormError = 'Please, fill all fields'
                return
            }
            const emailPattern = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
            if (!emailPattern.test(this.userData.email)) {
                this.loginFormIsValid = false
                this.loginFormError = 'Please enter valid email'
                return
            }
            this.loginFormError = ''
            this.loginFormIsValid = true
        },
        async submitAutorization () {
            const id = Date.now();
            const user = this.newUserData
            const payload = {
                id,
                ...user
            }
            try {
                axios.post('https://63d57925dc3c55baf428bf36.mockapi.io/database/users/', payload)
                .then(() => {
                    this.SET_USER_TO_STATE(payload)
                    this.isSubmited = true;
                    this.formError = 'Your registration is complete!';
                    setTimeout(() => {
                        this.$router.push({name: 'Profile'})
                    }, 1000);
                })
            } catch (error) {
                console.error('Error submitting form:', error);
            }
        },
        async submitLogin() {
            const credentials = {
                ...this.userData
            }
            const email = this.userData.email;
            const password = this.userData.password;
            try {
                const res = await axios.get('https://63d57925dc3c55baf428bf36.mockapi.io/database/users/', {
                    params: credentials
                })
                const user = res.data.find(user => user.email === email && user.password === password);
                if (user) {
                    this.SET_USER_TO_STATE(user)
                    this.isSubmited = true;
                    this.loginFormError = 'Welcome back!';
                    setTimeout(() => {
                        this.$router.push({name: 'Profile'})
                    }, 1000);
                } else {
                    this.loginFormError = 'Such user not found=(';
                }
            } catch (error) {
                console.log(error);
            }
        },
        cancelRegistration () {
            this.$router.go(-1);
        }
    },
    watch: {
            'newUserData.name'() {
                this.formValidation();
            },
            'newUserData.lastname'() {
                this.formValidation();
            },
            'newUserData.phone'() {
                this.formValidation();
            },
            'newUserData.email'() {
                this.formValidation();
            },
            'newUserData.country'() {
                this.formValidation();
            },
            'newUserData.address'() {
                this.formValidation();
            },
            'userData.email'() {
                this.loginValidation();
            },
            'userData.password'() {
                this.loginValidation();
            },
    },
}
</script>

