<template>
    <vHeader> </vHeader>

    <vOrderCondirmation
    v-if="showConfirmation"
    @closeOpenOrderConfirmation="closeOpenOrderConfirmation"
    />
    <div class="v-cart"
    :style="{ display: showConfirmation ? 'none' : 'block' }"
    >
        <h1>Cart</h1>
        <vCartItem
        v-for="(item, index) in GET_CART"
        :key="item.article"
        :cart_item_data="item"
        @deleteFromCart="deleteFromCart(index)"
        @decrement="decrementment(index)"
        @increment="increment(index)"
        >
        </vCartItem>

        <div v-if="!GET_CART.length" class="car-is-empty">
            <p>Cart is empty=(</p>

            <router-link :to="{name: 'Catalog'}">
                <button class="btn">
                    Cart's Looking a Bit Light?
                </button>
            </router-link>
        </div>

        <div class="v-cart__total">
            <div class="total-wrapper">
                <p class="total-name">Total</p>
                <p class="total-price">{{cartTotalCost}}$</p>
            </div>

            <button 
                    v-if="isCart"
                    class="commit-order-button"
                    @click="closeOpenOrderConfirmation"
                    >onfirm order
                    </button>

                    <button 
                    v-else
                    disabled
                    class="commit-order-button disabled"
                    >onfirm order
                    </button>
        </div>
    </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import vCartItem from './v-cart-item.vue'
import vHeader from './v-header.vue'
import vOrderCondirmation from './v-popup/v-order-confirmation.vue'

export default {
    name: "v-cart",
    data() {
        return {
            showConfirmation: false
        }
    },
    components: {
        vCartItem,
        vHeader,
        vOrderCondirmation
    },
    props: {
    },
    methods: {
        ...mapActions([
            'DELETE_FROM_CART',
            'INCREMENT_CART_ITEM',
            'DECREMENT_CART_ITEM'
        ]),
        deleteFromCart(index) {
            this.DELETE_FROM_CART(index)
        },
        decrementment (index) {
            this.DECREMENT_CART_ITEM(index);
        },
        increment (index) {
            this.INCREMENT_CART_ITEM(index);
        },
        closeOpenOrderConfirmation() {
            if (this.GET_CART.length === 0) {
                this.showConfirmation = false;
                return
            }
            this.showConfirmation = !this.showConfirmation
        }
    },
    computed: {
        ...mapGetters([
            'GET_CART',
        ]),
        cartTotalCost() {
            let result = []
            if (this.GET_CART.length) {
                result = this.GET_CART.reduce((sum, item) => {
                    return sum + item.price * item.quantity;
                }, 0);
                return result
            } else {
                result =  0
                return result;
            }
        },
        isCart () {
            if (this.GET_CART.length > 0) {
                return true
            }
            return false
        }
    }
}
</script>
