        letter-spacing: 2;
        font-size: 20px;
    }
    .total-wrapper {
        display: flex;
        margin-left: 10%;
    }
    .total-name{
        margin-left: 10%;
    }
    .total-price{
        margin-left: 15px;
    }
    .commit-order-button {
        padding: $padding $padding*2;
        font-family: $secondary-font;
        letter-spacing: 1px;
        font-size: 20px;
        background: $color-purple;
        color: #ffffff;
        border: 0;
        border-radius: $radius;
        outline: none;
        cursor: pointer;
        &:hover {
            background: white;
            color: $color-purple;
        }
        margin-right: 10%;
    }
    .commit-order-button.disabled {
        background: $color-disabled;
        &:hover {
            background: $color-disabled;
            color: #fff;
            cursor: default;
        }
    }
    .car-is-empty {
        margin-top: 10%;
        color: $color-purple;
    }
    .car-is-empty p {
        font-family: $secondary-font;
