<template>
    <div class='v-catalog-item'>
        <vBestSeller
        v-if="product_data.bestSeller === true"
        />
        <vPopup
            v-if="isPopupActive"
            @closePopup="closePopup"
            :item_data="product_data"
            @popupAddToCart="popupAddToCart"
        >
        </vPopup>
        <div class="catalog-item-wrapper">
            <div @click="showPopup" class="popup-button">
                <div class="catalog-item-img-wrapper">
                    <img :src=" require('../assets/images/' + product_data.image)" 
                    class="catalog-item-img" alt="">
                </div>
                <p class="v-catalog-item_name"><span class="title-span"><strong>{{product_data.name}}</strong></span></p>
            </div>
            <p class="v-catalog-category"><span class="pink-highlither">{{product_data.category}}</span></p>
            <p class="v-catalog-item_price"><span class="cian-highlither">Price:</span> <strong><span class="price-span">{{product_data.price}}$</span></strong></p>
            <p class="v-catalog-article"><span class="cian-highlither">Article:</span> <strong><span class="article-span">{{product_data.article}}</span></strong></p>
            <v-select
            class="catalog-select"
            :options="product_data"
            @selectValue="selectValue"
            />

            <button 
            v-if="checkSize"
            class="v-catalog-item_btn btn"
            @click="addToCart"
            >Add to cart
            </button>

            <button 
            v-else
            disabled
            class="v-catalog-item_btn btn disabled"
            >Choose size
            </button>
        </div>
    </div>
</template>

<script>
import vPopup from '../components/v-popup/v-popup.vue'
import vSelect from './v-select.vue'
import vBestSeller from '../components/v-design-components/v-best-seller.vue'

export default {
    name: "v-catalog-item",
    data() {
        return {
            isPopupActive: false,
            size: ''
        }
    },
    components: {
        vPopup,
        vSelect,
        vBestSeller
    },
    props: {
        product_data: {
            type: Object,
            default () {
                return {}
            },

        }
    },
    methods: {
        addToCart() {
            this.$emit('addToCart', this.product_data, this.size)
        },
        popupAddToCart(item_data, size) {
            this.size = size;
            this.$emit('addToCart', item_data, this.size)
        },
        showPopup() {
            this.isPopupActive = true;
        },
        closePopup() {
            this.size = '';
            this.isPopupActive = false;

        },
        selectValue(option) {
            this.size = option;
        }
    },
    computed: {
        checkSize () {
            if (this.size === '') {
                return false
            }
            return true
        }
    }
}
</script>

