<template lang="">
    <div class="v-cart-item">
            <div class="cart-image-wrapper">
                <img 
                :src=" require('../assets/images/' + cart_item_data.image)"
                class="cart-data-image"
                >
            </div>
        <div class="v-cart-item-info">
            <p>
                <span class="title-span"><strong>{{cart_item_data.name}}</strong></span>
                <span class="size-span"><strong>{{cart_item_data.sizes}}</strong></span> 
            </p>
            <p>Price: <span class="numbers-span">{{cart_item_data.price * this.cart_item_data.quantity}}$</span></p>
            <p>Article:
                <span class="numbers-span">{{cart_item_data.article}}</span>
            </p>
        </div>
        <div class="v-cart-item-quantity">
            <p>Quantity:</p>
            <span @click="decrement" class="quantity-changes">-</span>
            <span class="quantity-value">{{cart_item_data.quantity}}</span>
            <span @click="increment" class="quantity-changes">+</span>
        </div>
        <button
        class="btn cart-item-btn"
        @click="deleteFromCart">
            Delete
        </button>
    </div>
</template>

<script>
import {mapActions} from 'vuex';

export default {

    name: "v-cart-item",
    props: {
        cart_item_data: {
            type: Object,
            default () {
                return {}
            }
        }
    },
    methods: {
        ...mapActions([
            'ADD_TO_CART',
            'DELETE_FROM_CART'
        ]),
        deleteFromCart() {
            this.$emit('deleteFromCart')
        },
        decrement() {
            this.$emit('decrement')
        },
        increment () {
            this.$emit('increment')
        }
    }
}
</script>

