<template>
    <div class="v-catalog">
        <vHeader
        />
        <h1 class="catalog-h1">Beautyfull 
            <span class="h1-span">T-SHIRTS</span> 
            for real memsters
        </h1>

        <div class="catalog-filters">
            <div class="catalog-filter categories">
                <reusableSelect
                :selectName="'Categories'"
                :selectData="categories"
                @selectedValue="selectedValue"
                />
            </div>
            <div class="catalog-filter size">
                <reusableSelect
                :selectName="'Size'"
                :selectData="sizes"
                @selectedValue="selectedValue"
                />
            </div>
            <div class="catalog-filter price">
                <reusableRangeSelect
                :selectName="'Price'"
                :selectData="computePrice"
                @selectedRange="selectedValue"
                />
            </div>
        </div>
        <div class="v-catalog-wrapper">  
            <vCatalogItem
                v-for="product in paginatedProducts"
                :key="product.article"
                :product_data="product"
                @addToCart="addToCart"
            />
        </div>
        <div class="pagination-wrapper">
            <div 
            class="loadMoreProducts"
            @click="loadMoreProducts"
            >
                <p>
                    Show more...
                </p>
                <i class="material-symbols-outlined">
                    autorenew
                </i>
            </div>
            <ul class="pagination">
                <li 
                class="pagination-prev-page"
                :class="{ disabled: currentPage === 1 }"
                @click="prevPage"
                >
                    <i class="material-symbols-outlined">
                        arrow_back_ios
                    </i>
                </li>
                <li 
                class="pagination-pages"
                :class="{ active: pageNumber === currentPage }"
                v-for="pageNumber in paginationPagesTotal"
                :key="pageNumber"
                @click="toDirectPage(pageNumber)"
                >
                    {{ pageNumber }}
                </li>
                <li 
                class="pagination-pages last-page"
                :class="{ disabled: currentPage === paginationPagesTotal }"
                @click="toLastPage"
                >
                    ...
                </li>
                <li 
                class="pagination-next-page"
                :class="{ disabled: currentPage === paginationPagesTotal }"
                @click="nextPage"
                >
                    <i class="material-symbols-outlined">
                        arrow_forward_ios
                    </i>
                </li>
            </ul>
        </div>
    </div>
</template>
<script>
import { mapActions, mapGetters } from 'vuex';
import vHeader from '../components/v-header.vue';
import vCatalogItem from '../components/v-catalog-item.vue';
import reusableSelect from './reusable-select.vue';
import reusableRangeSelect from './v-range-slider.vue'

export default {
    name: "v-catalog",
    components: {
        vCatalogItem,
        vHeader,
        reusableSelect,
        reusableRangeSelect
    },
    data() {
        return {
            categories: [
                'All',
                'Men',
                'Woman'
            ],
            sizes: [
                'All', 'XS', 'S', 'M', 'L', 'XL', 'XXL'
            ],
            selectedSize: 'All',
            selectedCategory: 'All',
            itemsPerPage: 11,
            currentPage: 1,
            selectedPriceMin: null,
            selectedPriceMax: null,
            filteredProducts: []
        }
    },
    computed: {
        ...mapGetters([
            'GET_PRODUCTS'
        ]),
        computePrice() {
            let price = new Set();
            this.GET_PRODUCTS.forEach(product => {
                price.add(Math.floor(product.price))
            });
            price = [...price].sort();
            return price
        },
        paginationPagesTotal () {
            return Math.ceil(this.filteredProducts.length / this.itemsPerPage)
        },
        paginatedProducts() {
            const startIndex = (this.currentPage - 1) * this.itemsPerPage;
            const endIndex = startIndex + this.itemsPerPage;
            return this.filteredProducts.slice(startIndex, endIndex);
        }
    },
    methods: {
        ...mapActions([
            'GET_PRODUCTS_FROM_API',
            'ADD_TO_CART'
        ]),
        addToCart(data, size) {
            const product = {
                ...data,
                sizes: size
            };
            this.ADD_TO_CART(product);
        },
        selectedValue(option, filterName) {
            if (filterName === 'Categories') {
                this.selectedCategory = option;
            }
            if (filterName === 'Size') {
                this.selectedSize = option
            }
            if (filterName === 'Price') {
                this.selectedPriceMin = option[0];
                this.selectedPriceMax = option[1];
            }
        },
        filterProducts() {
            let products = this.GET_PRODUCTS;
            const scope = this
            const filteredProducts = products.filter((product) => {
                const sizeMatch = scope.selectedSize === 'All' || product.sizes.includes(scope.selectedSize);
                const categoryMatch = scope.selectedCategory === 'All' || product.category === scope.selectedCategory;
                const priceMatch =
                    (scope.selectedPriceMin === null || product.price >= scope.selectedPriceMin) &&
                    (scope.selectedPriceMax === null || product.price <= scope.selectedPriceMax);
                this.currentPage = 1;
                return sizeMatch && categoryMatch && priceMatch
            })
            this.filteredProducts = [...filteredProducts];
        },
        prevPage() {
            if (this.currentPage > 1) {
                this.currentPage--;
            }
        },
        nextPage() {
            if (this.currentPage < this.paginationPagesTotal) {
                this.currentPage++;
                window.scroll({
                    top: 0,
                    left: 0,
                    behavior: 'smooth'
                });
            }
        },
        toLastPage() {
            if (this.currentPage != this.paginationPagesTotal) {
                this.currentPage = this.paginationPagesTotal;
                window.scroll({
                    top: 0,
                    left: 0,
                    behavior: 'smooth'
                });
            }
        },
        toDirectPage(pageNumber) {
            if (pageNumber != this.currentPage) {
                this.currentPage = pageNumber;
                window.scroll({
                    top: 0,
                    left: 0,
                    behavior: 'smooth'
                });
            }
        },
        loadMoreProducts() {
            if (this.itemsPerPage * this.currentPage <= this.filteredProducts.length) {
                this.itemsPerPage += 11;
            }
        }
    },
    watch: {
        'selectedSize'() {
            this.filterProducts();
        },
        'selectedCategory'() {
            this.filterProducts();
        },
        'selectedPriceMin'() {
            this.filterProducts();
        },
        'selectedPriceMax'() {
            this.filterProducts();
        },
    },
    mounted() {
        this.GET_PRODUCTS_FROM_API()
        .then((res) => {
            this.filteredProducts = res.data
        })
        .catch(err => 
        console.log(err));
    }
}
</script>

