<template>
    <div class="select-wrapper"
    v-bind:class="{ active: isSelectShown }"
    >
        <div class="categories-select-title"
        @click="showSelect"
        >
            <p>{{selectName}}</p> 
            <i class="material-symbols-outlined">
                expand_more
            </i>
        </div>
        <ul class="categories-select-body"
        v-if="isSelectShown"
        >
            <li
            v-for="(option, index) in selectData" 
            :key="index"
            @click="selectedValue(option)"
            >
            {{ option }}
            </li>
        </ul>
        <div
        v-else
        class="selected-value-handler"
        @click="showSelect"
        >
            {{ selected }}
        </div>
    </div>
</template>

<script>
export default {
    name: 'reusableSelect',
    props: {
        selectName: {
            type: String,
            default() {
                return ''
            }
        },
        selectData: {
            type: Object,
            default() {
                return {}
            }
        }
    },
    data() {
        return {
            isSelectShown: false,
            selected: 'All',
        }
    },
    methods: {
        selectedValue(option) {
            this.selected = option;
            this.$emit('selectedValue', option, this.selectName);
        },
        showSelect() {
            if (!this.isSelectShown) {
                this.isSelectShown = true;
                const eventListenerTimeout = setTimeout(() => {
                    window.document.addEventListener('click', this.closeSelect);
                    clearTimeout(eventListenerTimeout)
                }, 200);
            }
        },
        closeSelect() {
            if (this.isSelectShown) {
                this.isSelectShown = false;
                window.document.removeEventListener('click', this.closeSelect);
            }
        }
    }
}
</script>

