<template>
    <div class="order-wrapper">
        <div
        class="order-item-wrapper"
        v-for="(order, index) in this.orders.orderProducts"
        :key="index"
        >
            <p class="order-discription">
                {{ order.name }} {{ order.sizes }} x <span class="order-highlither">{{order.quantity}}pcs</span>.
            </p>
            <p class="order-article">
                <span class="order-highlither">{{ order.article }}</span>
            </p>
            <div class="order-image-wrapper">
                <img :src=" require('@/assets/images/' + order.image)" alt="">
            </div>
        </div>
    </div>
    <div class="order-summary">
        <p class="order-price">
            Total amount: {{ totalPrice }}$
        </p>
    </div>
    <div class="order-border"> </div>
</template>

<script>
export default {
    name: 'vProfileOrder',
    props: {
        orders: {
            type: Object,
            default () {
                return {

                }
            }
        }
    },
    data() {
        return {
            userOrder: {

            }
        }
    },
    computed: {
        totalPrice() {
            let orderData = this.orders.orderProducts;
            let sum = [];
            for (let i = 0; i < orderData.length; i++) {
                sum.push(orderData[i].quantity * orderData[i].price);
            }
            return sum.reduce((totalAmount, currentValue) => totalAmount += currentValue)
        }
    },
    mounted() {
    },
}
</script>

