<template lang="html">
    <div class="popup">
        <div class="popup-header">
            <h2>{{item_data.name}}</h2>
            <span 
                class="material-icons md-36 pink bold pointer icon-hover"
                @click="closePopup"
            >
                <i>close</i>
            </span>
        </div>
        <div class="popup-content">
            <div class="popup-image-wrapper">
                <img :src=" require('../../assets/images/' + item_data.image)" 
                class="popup-item-img" alt="">
            </div>
            <div class="popup-description-wrapper">
                <div>
                    <h2><span class="pink-highlither">The {{item_data.name}} is made for real esthetes</span></h2>
                    <p><span class="cian-highlither">{{item_data.name}} is made with 100% cotton for keep your armpits fresh. The fancy print on it will tell everyone you'll met how much you know modern culture. </span></p>
                </div>
                <div class="popup-price-wrapper">
                    <p>Article: {{item_data.article}}</p>
                    <p class="popup-price">Price: {{item_data.price}}$</p>
                </div>
                <div class="popup-footer">
                    <v-select
                        :options="item_data"
                        @selectValue="selectValue"
                    />

                    <button 
                    v-if="checkSize"
                    class="popup-add-to-cart"
                    @click="popupAddToCart"
                    >Add to cart
                    </button>

                    <button 
                    v-else
                    disabled
                    class="popup-add-to-cart disabled"
                    >Choose size
                    </button>
                </div>
            </div>
        </div>

        
    </div>
</template>

<script>
import vSelect from '../v-select.vue'
import { mapGetters } from 'vuex'

export default {
    name: "v-popup",
    data() {
        return {
            size: ''
        }
    },
    components: {
        vSelect
    },
    props: {
        item_data: {
            type: Object,
            default () {
                return {}
            },
        }
    },
    computed: {
        ...mapGetters([
            'GET_PRODUCTS'
        ]),
        checkSize () {
            if (this.size === '') {
                return false
            }
            return true
        }
    },
    methods: {
        closePopup() {
            this.$emit('closePopup');
        },
        selectValue(option) {
            this.size = option;
        },
        popupAddToCart() {
            this.$emit('popupAddToCart', this.item_data, this.size)
        },
    }
}
</script>

