                            class="order-item-image"
                            :src="require('../../assets/images/' + item.image)" 
                            alt="">
                        </div>
                        <div class="order-item-description">
                            <p class="order-item-name">{{item.name}} {{item.sizes}}</p>
                            <p class="order-item-price">{{item.price}}$ <span>x</span> {{item.quantity}} piece<span v-if="item.quantity>1" style="color: #eea9d4">s</span></p>
