<script>
import { mapGetters, mapMutations, mapActions } from 'vuex';
import axios from 'axios';

export default {
    name: 'vOrderConfiramtion',
    data () {
        return {
            formData: {
                name: '',
                lastname: '',
                phone: null,
                email: '',
                country: '',
                address: '',
            },
            userData: {
                email: '',
                password: ''
            },
            formError: '',
            loginFormError: '',
            formIsValid: false,
            loginFormIsValid: false,
            isSubmited: false
        }
    },
    computed: {
        ...mapGetters([
            'GET_CART',
            'GET_USER'
        ]),
        cartTotalCost() {
            let result = []
            if (this.GET_CART.length) {
                result = this.GET_CART.reduce((sum, item) => {
                    return sum + item.price * item.quantity;
                }, 0);
                return result
            } else {
                result =  0
                return result;
            }
        },
    },
    methods: {
        ...mapMutations([
            'CLEAR_CART'
        ]),
        ...mapActions([
            'SET_USER_TO_STATE'
        ]),
        submitForm () {
            const id = Date.now();
            const customer = this.formData
            const orderProducts = this.GET_CART
            const payload = {
                id,
                customer,
                orderProducts
            }
            axios.post('https://63d57925dc3c55baf428bf36.mockapi.io/database/orders/', payload)
            .then(() => {
                this.isSubmited = true;
                this.formError = 'your order complete succesfuly';
                this.formData.name = '';
                this.formData.lastname = '';
                this.formData.phone = null;
                this.formData.email = '';
                this.formData.country = '';
                this.formData.address = '';
                this.formIsValid = false;
                this.CLEAR_CART()
            })
            .catch((error) => {
                console.error('Error submitting form:', error);
            });
        },
        formValidation() {
            //check is fields is filled 
            if (
                this.isSubmited ||
                !this.formData.name || 
                !this.formData.lastname || 
                !this.formData.phone || 
                !this.formData.email || 
                !this.formData.country ||
                !this.formData.address
            ) {
                this.formIsValid = false
                this.formError = 'Please, fill all fields'
                return
            }
             //check type of number
            let phone = parseInt(this.formData.phone)
            if (isNaN(phone)) {
                this.formIsValid = false
                this.formError = 'Please enter valid phone number'
                return
            }
            this.formError = ''
            //check email validity
            const emailPattern = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
            if (!emailPattern.test(this.formData.email)) {
                this.formIsValid = false
                this.formError = 'Please enter valid email'
                return
            }
            this.formError = ''
            this.formIsValid = true
        },
        loginValidation() {
            if (this.isSubmited) {
                return
            }
            if (
                !this.userData.email || 
                !this.userData.password
            ) {
                this.loginFormIsValid = false
                this.loginFormError = 'Please, fill all fields'
                return
            }
            const emailPattern = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
            if (!emailPattern.test(this.userData.email)) {
                this.loginFormIsValid = false
                this.loginFormError = 'Please enter valid email'
                return
            }
            this.loginFormError = ''
            this.loginFormIsValid = true
        },
        async submitLogin() {
            const email = this.userData.email || this.GET_USER.email;
            const password = this.userData.password || this.GET_USER.password;
            const credentials = {
                email,
                password
            }
            try {
                const res = await axios.get('https://63d57925dc3c55baf428bf36.mockapi.io/database/users/', {
                    params: credentials
                })
                const user = res.data.find(user => user.email === email && user.password === password);
                if (user) {
                    this.SET_USER_TO_STATE(user)
                    this.isSubmited = true;
                    this.loginFormError = 'Welcome back!';
                    const id = Date.now();
                    const customer = this.GET_USER
                    const orderProducts = this.GET_CART
                    const payload = {
                        id,
                        ...customer,
                        orderProducts
                    }
                    axios.post('https://63d57925dc3c55baf428bf36.mockapi.io/database/orders/', payload)
                    .then(() => {
                        this.isSubmited = true;
                        this.formError = 'your order complete succesfuly';
                        this.CLEAR_CART()
                    })
                    .catch((error) => {
                        console.error('Error submitting form:', error);
                    });
                } else {
                    this.loginFormError = 'Such user not found=(';
                }
            } catch (error) {
                console.error(error);
            }
        },
        closePopup() {
            this.$emit('closeOpenOrderConfirmation')
        }
    },
    watch: {
        'formData.name'() {
            this.formValidation();
        },
        'formData.lastname'() {
            this.formValidation();
        },
        'formData.phone'() {
            this.formValidation();
        },
        'formData.email'() {
            this.formValidation();
        },
        'formData.country'() {
            this.formValidation();
        },
        'formData.address'() {
            this.formValidation();
        },
        'userData.email'() {
            this.loginValidation();
        },
        'userData.password'() {
            this.loginValidation();
        },
    }
}
</script>

