<template>
    <div class="component-select"
    >
        <p class="select-title"
        @click="optionsAreVisible = !optionsAreVisible"
        >
            {{selectTitle}}
        </p>
        <div class="options"
        v-if="optionsAreVisible"
        >
            <p
            v-for="size in options.sizes"
            :key="size"
            @click="selectOption(size)"
            >
                {{ size }}
            </p>

        </div>
    </div>
</template>

<script>
export default {
    name: "vSelect",
    data() {
        return {
            optionsAreVisible: false,
            selectTitle: "Size:"
        }
    },
    props: {
        options: {
            type: Object,
            default () {
                return {}
            }
        }
    },
    methods: {
        selectOption(option) {
            this.selectTitle = option;
            this.optionsAreVisible = false;
            this.$emit('selectValue', option);
        },
        hideSelect() {
            this.optionsAreVisible = false;
        }
    },
    mounted() {
        document.addEventListener('click', this.hideSelect.bind(this), true)
    },
    beforeUnmount() {
        document.removeEventListener('click', this.hideSelect.bind(this))
    },
}
</script>

