    }
    .component-select p {
        margin: 0;
    }
    .options {
        position: absolute;
        z-index: 10;
        top: 50px;
        left: 50%;
        transform: translateX(-50%);
    }
    .options p {
        margin-top: 3px;
        max-width: 100%;
        padding: 5px 10px;
        background: $color-cian;
    }
    .component-select:hover, .options p:hover{
