<template>
    <div class="select-wrapper"
    >
        <div class="categories-select-title"
        @click="showSelect = !showSelect"
        >
            <p>{{selectName}}</p> 
            <i class="material-symbols-outlined">
                expand_more
            </i>
        </div>
        <div 
        v-if="showSelect"
        class="range-slider-input-wrapper"
        >
            <input 
            type="text" 
            v-model="minPrice"
            name="minPrice"
            :placeholder="minPrice"
            @change="selectedRange"
            required
            class="minPriceMonitor"
            />
            <div 
            v-if="deviceWidth > 500"
            class="range-slider"
            >
                <input 
                type="range"
                min="0"
                max="200"
                step="1"
                v-model.number="minPrice"
                @change="selectedRange"
                >
                <input 
                type="range"
                min="0"
                max="100"
                step="1"
                v-model.number="maxPrice"
                @change="selectedRange"
                >
            </div>
            <input 
            type="text" 
            v-model="maxPrice"
            name="maxPrice"
            :placeholder="maxPrice"
            @change="selectedRange"
            required
            class="maxPriceMonitor"
            />
        </div>
    </div>
</template>

<script>
export default {
    name: 'reusableRangeSelect',
    props: {
        selectName: {
            type: String,
            default() {
                return ''
            }
        },
        selectData: {
            type: Object,
            default() {
                return {}
            }
        }
    },
    data() {
        return {
            showSelect: false,
            minPrice: 0,
            maxPrice: 100,
            deviceWidth: 0
        }
    },
    methods: {
        selectedRange() {
            if (this.minPrice >= 100) {
                this.minPrice = 100-10
            }
            if (this.maxPrice <= 0) {
                this.maxPrice = 10
            }
            if (this.maxPrice <= this.minPrice) {
                this.maxPrice = this.minPrice+10
            }
            if (this.minPrice >= this.maxPrice) {
                this.minPrice = this.maxPrice-10
            }
            this.$emit('selectedRange', [this.minPrice, this.maxPrice], this.selectName)
        }
    },
    created () {
        this.deviceWidth = window.innerWidth;
    }
}
</script>

