<script>
import vHeader from './v-header.vue';
import vProfileOrder from './v-popup/v-profile-order.vue'

import axios from 'axios';
import { mapGetters, mapActions } from 'vuex';

export default {
    name: 'vProfile',
    components: {
        vHeader,
        vProfileOrder
    },
    data() {
        return {
            whatSection: 'userProfile',
            orders: [],
            name: '',
            lastname: '',
            phone: '',
            address: '',
            password: '',
            isLogOutActive: false,
            deviceWidth: 0
        }
    },
    computed: {
        ...mapGetters([
            'GET_USER'
        ])
    },
    methods: {
        ...mapActions([
            'LOGOUT_USER',
            'SET_USER_TO_STATE'
        ]),
        async getOrders() {
            const email = this.GET_USER.email;
            const password = this.GET_USER.password;
            const credentials = {
                email,
                password
            }
            try {
                const res = await axios.get('https://63d57925dc3c55baf428bf36.mockapi.io/database/orders/', {
                    params: credentials
                })
                const orders = res.data.filter(user => user.email === email && user.password === password);
                if (orders) {
                    this.orders = orders
                }
            } catch (error) {
                console.log(error);
            }
        },
        userLogOut() {
            this.isLogOutActive = true;
        },
        logOutUser() {
            this.$router.push('Home');
            setTimeout(() => {
                this.LOGOUT_USER();
            }, 1000);
        },
        userChangeData(newData, field) {
            const userId = this.GET_USER.id
            const payload = {
                [field]: newData
            }
            try {
                axios.put(`https://63d57925dc3c55baf428bf36.mockapi.io/database/users/${userId}`, payload)
                .then((res) => {
                    this.SET_USER_TO_STATE(res.data);
                    this[field] = '';
                })
            } catch (error) {
                console.error('Error submitting form:', error);
            }
        },
        changeName() {
            this.userChangeData(this.name, 'name');
        },
        changeLastname() {
            this.userChangeData(this.lastname, 'lastname');
        },
        changePhone() {
            this.userChangeData(this.phone, 'phone');
        },
        changeAddress() {
            this.userChangeData(this.address, 'address');
        },
        changePassword() {
            this.userChangeData(this.password, 'password');
        }
    },
    mounted() {
        this.getOrders()
    },
    created () {
        this.deviceWidth = window.innerWidth;
    }
}
</script>

