import { createRouter, createWebHistory } from 'vue-router'

import vCatalog from '../components/v-catalog.vue';
import vCart from '../components/v-cart.vue';
import vHome from '../components/v-home.vue';
import vAutorization from '../components/v-autorization.vue';
import vProfile from '../components/v-profile.vue';
import store from '@/vuex/store';


const router = createRouter({
    history: createWebHistory(),
    routes: [
        {
            path: '/',
            name: 'Home-Page',
            component: vHome,
            props: true,
            meta: { from: 'Home-Page' },
        },
        {
            path: '/catalog',
            name: 'Catalog',
            component: vCatalog,
            meta: { from: 'Catalog' },
        },
        {
            path: '/cart',
            name: 'Cart',
            component: vCart,
            props: true,
            meta: { from: 'Cart' },
        },
        {
            path: '/autorization',
            name: 'Autorization',
            component: vAutorization,
            props: true,
            beforeEnter: (to, from, next) => {
                if (store.state.user) {
                  next('/profile');
                } else {
                  next();
                }
            }
        },
        {
            path: '/profile',
            name: 'Profile',
            component: vProfile,
            meta: { requiresAuth: true },
            props: true
        }
    ]
});

router.beforeEach((to, from, next) => {
    if (to.meta.requiresAuth && !store.state.user) {
      next('/autorization');
    } else {
      if (to.meta) {
        to.meta.fromComponent = from.name;
      }
      next();
    }
});

export default router
  